import { createApp } from 'vue'
// import App from './App.vue'

import { createRouter, createWebHistory } from 'vue-router'
import TodoList from "./components/TodoList.vue";
import MessengerOnline from "./components/MessengerOnline.vue";

const router = createRouter({
  history: createWebHistory(),
  routes: [
    {
      path: '/',
      name: '',
      component: MessengerOnline
    },
    {
      path: '/TodoList',
      name: 'TodoList',
      component: TodoList
    },
    {
      path: '/Messenger',
      name: 'Hello',
      component: MessengerOnline
    }
  ]
})


createApp(MessengerOnline)
.use(router)
.mount('#app')

