<template>
 <div class="main">
  <h3>Мессенджер</h3>

  <form class="form" >
    <!--<input class="input" v-model="description" type="text" name="description" placeholder="Введите сообщение" />-->
    <div id="message-field">
      <textarea class="inputMessenger" v-model="description" type="text" name="description" placeholder="Введите сообщение" />
      <button class="submit-button-mes" @click="addMessage">></button>
    </div>
  </form>
  <div class="messages-container">
    <ul>
      <li v-for="(message) in messages.slice().reverse()" :key="message._id">
        <div class="message">
          <span class="message-author"><b>{{ message.author }}</b></span>
          <span class="message-description"><textarea class="message-description-textarea" v-model=message.description></textarea></span>
          <span class="message-date">{{ message.date }}</span>
        </div>
      </li>
    </ul>
  </div>

</div>
</template>

<script>
import axios from "axios";
export default {
  name: "App",
  data() {
    return {
      messages: [],
      description: "",
      date: "",
    };
  },
  async mounted() {
    const response = await axios.get("/api/messenger/");
    this.messages = response.data;
  },
  methods: {
    async addMessage(e) {
      e.preventDefault();
      const response = await axios.post("api/messenger/", {
        author: "",
        description: this.description,
        date: this.currentDateTime(),
      });
      this.messages.push(response.data);
      this.author = "";
      this.description = "";
      this.date = "";
    },

    currentDateTime() {
      const current = new Date();
      const date = current.getFullYear()+'-'+(current.getMonth()+1)+'-'+current.getDate();
      const time = current.getHours() + ":" + current.getMinutes() + ":" + current.getSeconds();
      const dateTime = date +' '+ time;

      return dateTime;
    }
  }
};
</script>
  
<style>
body {
    background-color: black;
    color: white;
}
.main {
  width: 80%;
  color: white;
  margin: auto;
  border: 1px solid white;
  border-radius: 10px;
}

textarea {
  width: 100%;
  background-color: black;
  color: white;
  border-radius: 10px;
  padding: 10px;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

 h3{
  font-size: 22px;
  font-weight: bold;
  text-align: center;
}

.inputMessenger {
  width: 100%;
  padding: 10px;
  background-color: black;
  color: white;
  border-right: none;
  border-radius: 10px;
}

.submit-button-mes {
  padding: 15px;
  background-color: #1976d2;
  color: white;
  cursor: pointer;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}

#message-field {
  display: flex;
  width: 100%;
  border-right: 1px solid white;
  border-left: 1px solid white;
  border-left: 1px solid white;
  border-radius: 13px;
  background-color: black;
  padding: 10px;
}

.messages-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: url(@/assets/back.jpg);
  border-radius: 10px;
}

.messages-container ul {
  width: 100%;
  list-style: none;
  padding: 0;
}

.messages-container ul li {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  border: 1px solid white;
  color: white;
  background-color: black;
  margin-bottom: 1%;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
  border-top-right-radius: 20px;
  margin-left: 1%;
}

.message {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
  padding-right: 8%;
}

.message-author {
  font-size: 18px;
  font-weight: bold;
}

.message-description {
  /*max-width: 70%;*/
  font-size: 14px;
  display: contents;
}

.message-date {
  color: #07d1ff;
  float: right;
  font-size: 13px;
  margin-left: 100%;
  width: max-content;
}

.delete-btn {
  background-color: #f44336;
  color: white;
  padding: 10px;
  cursor: pointer;
  border: none;
}
</style>